header {
    position: absolute;
    top: 0;
    width: 100%;
    display: flex;
    align-items: center;
    z-index: 5;
    justify-content: center;
}

header.white {
    border-bottom: 2px solid var(--grey-10);
}

header.white .logo__container {
    background-color: black;
    color: white;
}

header.black nav a {
    color: white;
}

header.black .logo__container {
    background-color: white;
}

header a {
    display: inherit;
    align-items: inherit;
}